* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
}

img,
video {
  max-width: 100%;
  height: auto;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

button,
input,
textarea {
  font: inherit;
  border: none;
  background: none;
  padding: 0;
}

body {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  color: #5a6770;
}

.section__btn {
  border-radius: 8px;
  background-color: #28445d;
  padding: 14px;
  width: 100%;
  color: #fff;
  cursor: pointer;
  display: block;
  transition: all 0.4s;
}
.section__btn:hover {
  color: #28445d;
  background-color: #e6f4f1;
}
.section__titile {
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 36px;
  line-height: 120%;
  color: #53575a;
}
.section__subtitle {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  color: #5a6770;
}
.section__column {
  max-width: 586px;
}

.container {
  max-width: 1240px;
  margin: 0 auto;
}

.header {
  height: 100vh;
  padding: 24px;
  margin-bottom: 180px;
}
.header__wrapper {
  display: flex;
  height: 100%;
}
.header__column {
  max-width: 50%;
  width: 100%;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
}
.header__column--green {
  background: #e6f4f2;
  padding: 80px;
}
.header__logo {
  display: flex;
  justify-content: center;
  margin-bottom: 131px;
  padding-top: 14px;
}
.header__text {
  max-width: 747px;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 49px;
}
.header__title {
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 48px;
  line-height: 120%;
  color: #53575a;
  margin-bottom: 20px;
}
.header__subtitle {
  max-width: 542px;
  margin: 0 auto;
}
.header__form {
  box-shadow: 0 24px 41px -16px rgba(47, 45, 86, 0.1), 0 -16px 40px -36px rgba(47, 45, 86, 0.1);
  background: #fff;
  border-radius: 20px;
  max-width: 379px;
  padding: 26px 23px;
  margin: 0 auto;
}
.header__link {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-top: auto;
}
.header__text2 {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  text-align: center;
  color: #5a6770;
  margin-bottom: 54px;
}
.header__messages {
  margin: 0 auto;
  max-width: 413px;
  width: 100%;
}
.header__calendar {
  border-radius: 20px;
  overflow: hidden;
}
.header__calendar img {
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.header-form__btn {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #53575a;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 9px;
  margin-bottom: 19px;
  border: 0.81px solid #d4e6f0;
  border-radius: 8px;
  padding: 12px;
}
.header-form__description {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 12px;
  line-height: 140%;
  letter-spacing: -0.01em;
  text-align: center;
  color: #5a6770;
}
.header-form__description span {
  text-decoration: underline;
}

.header-message {
  display: flex;
  gap: 13px;
  margin-bottom: 19px;
  max-width: 412px;
}
.header-message__text {
  padding: 13px;
  border-radius: 0 9px 9px 9px;
  background-color: #fff;
  max-width: 303px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #5a6770;
}
.header-message__img {
  width: 41px;
  height: 41px;
  overflow: hidden;
  border-radius: 8px;
}
.header-message__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.header-message__text-right {
  border-radius: 0 9px 9px 9px;
}
.header-message__text-left {
  border-radius: 9px 0px 9px 9px;
}

.header-message.message-right {
  justify-self: flex-end;
}

.header-message.message-left {
  justify-self: flex-start;
}

.meet {
  margin-bottom: 204px;
}
.meet__title {
  text-align: center;
  margin-bottom: 20px;
}
.meet__subtitle {
  text-align: center;
  max-width: 685px;
  margin: 0 auto;
  margin-bottom: 60px;
}
.meet__wrapper {
  display: flex;
  justify-content: space-between;
}
.meet__column {
  display: flex;
  flex-direction: column;
}

.meet-accordeon {
  width: 100%;
}
.meet-accordeon__item {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  color: #939ba0;
}
.meet-accordeon__item:not(:first-child) {
  border-top: 1px solid #e2ecf0;
}
.meet-accordeon__header {
  padding: 22px 0;
  cursor: pointer;
  transition: all 0.4s;
}
.meet-accordeon__header:hover {
  color: #28445d;
}
.meet-accordeon__body {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: max-height 0.5s ease-out, opacity 0.5s ease-out;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #5a6770;
}

.meet-accordeon__item:first-child > .meet-accordeon__header {
  padding: 0 0 20px 0;
}

.meet-accordeon__item.active:first-child > .meet-accordeon__header {
  padding: 0 0 8px 0;
}

.meet-accordeon__item.active {
  padding-bottom: 20px;
}

.meet-accordeon__item.active .meet-accordeon__header {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 18px;
  line-height: 140%;
  color: #53575a;
  padding: 8px 0 8px 0;
}

.meet-accordeon__item.active .meet-accordeon__body {
  max-height: 150px;
  opacity: 1;
}

.meet-form__title {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 18px;
  line-height: 140%;
  color: #53575a;
  margin-bottom: 8px;
}
.meet-form__sunbtitle {
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 140%;
}
.meet-form__input {
  border-top: 1px solid #e2ecf0;
  padding: 22px 0;
  display: flex;
}
.meet-form__input label {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  color: #939ba0;
  white-space: nowrap;
  margin-right: 10px;
}
.meet-form__input input {
  outline: none;
  width: 100%;
}
.meet-form__btn {
  max-width: 242px;
  padding: 12px 0;
  margin-top: auto;
}

.smart {
  margin-bottom: 180px;
}
.smart__wrapper {
  display: flex;
  justify-content: space-between;
}
.smart__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 130%;
  margin-bottom: 20px;
}
.smart__subtitle {
  max-width: 586px;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: -0.01em;
}
.smart__list {
  margin-bottom: 8px;
}
.smart__list li {
  padding: 22px 0;
  color: #939ba0;
}
.smart__list li h6 {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 18px;
  line-height: 140%;
  color: #53575a;
  margin-bottom: 8px;
}
.smart__list li p {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #5a6770;
}
.smart__list li:not(:last-child) {
  border-bottom: 1px solid #e2ecf0;
}
.smart__list li:first-child {
  padding: 0;
  padding-bottom: 22px;
}
.smart__btn {
  max-width: 212px;
  padding: 12px 0;
  margin-top: auto;
}
.smart__card-wrapper {
  position: relative;
}
.smart__card {
  position: relative;
  background-color: #e6f4f1;
  padding: 25px 30px;
  border-radius: 20px;
  max-width: 100%;
  width: 590px;
  height: 100%;
  overflow: hidden;
}
.smart__description {
  font-style: italic;
  font-weight: 400;
  font-size: 28px;
  line-height: 150%;
  color: #53575a;
  margin-bottom: 171px;
  max-width: 343px;
}
.smart__author {
  font-family: var(--third-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  color: #5a6770;
}
.smart__column {
  display: flex;
  flex-direction: column;
}
.smart__decor-quotation {
  position: absolute;
  top: -25px;
  right: 40px;
}

.smart-accordeon {
  width: 100%;
}
.smart-accordeon__item {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  color: #939ba0;
}
.smart-accordeon__item:not(:first-child) {
  border-top: 1px solid #e2ecf0;
}
.smart-accordeon__header {
  padding: 22px 0;
  cursor: pointer;
  transition: all 0.4s;
}
.smart-accordeon__header:hover {
  color: #28445d;
}
.smart-accordeon__body {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: max-height 0.5s ease-out, opacity 0.5s ease-out;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #5a6770;
}

.smart-accordeon__item.active {
  padding-bottom: 20px;
}

.smart-accordeon__item.active .smart-accordeon__header {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 18px;
  line-height: 140%;
  color: #53575a;
  padding-bottom: 8px;
}

.smart-accordeon__item.active .smart-accordeon__body {
  max-height: 150px;
  opacity: 1;
}

.integrations {
  margin-bottom: 157px;
}
.integrations__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.integrations__columns {
  display: flex;
  gap: 20px;
}
.integrations__column--margin-top {
  margin-top: 70px;
}
.integrations__title {
  margin-bottom: 20px;
  margin-top: -14px;
}
.integrations__subtitle {
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 150%;
  color: #5a6770;
}
.integrations__btn {
  max-width: 242px;
}

.integrations-item {
  width: 182px;
  height: 189px;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 24px 41px -16px rgba(47, 45, 86, 0.1);
  border-radius: 9px;
}
.integrations-item__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.integrations-item__img {
  width: 50px;
}
.integrations-item__title {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 130%;
  text-align: center;
  color: #505050;
  max-width: 115px;
  margin-top: 15px;
}
.integrations-item__logos {
  display: grid;
  grid-template-columns: 30px 30px;
  gap: 7px;
}

.accolades {
  margin-bottom: 189px;
}
.accolades__title {
  margin-bottom: 60px;
  display: flex;
  justify-content: center;
}
.accolades__columns {
  display: flex;
  margin: 0 auto;
  margin-left: 94px;
}

.accolades-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.accolades-item:not(:last-child) {
  border-right: 1px solid #e2ecf0;
  margin-right: 80px;
  padding-right: 91px;
}
.accolades-item:first-child {
  padding: 0 87px;
  margin-right: 87px;
}
.accolades-item__grade {
  margin-top: auto;
  display: flex;
  align-items: center;
  gap: 6px;
  font-family: var(--third-family);
  font-weight: 700;
  font-size: 30px;
  line-height: 150%;
  color: #ffbc00;
}
.accolades-item__grade img {
  width: 24px;
  height: 24px;
}
.accolades-item__subtitle {
  font-weight: 500;
  font-size: 20px;
  line-height: 122%;
  color: #5a6770;
}

.plans {
  margin-bottom: 176px;
}
.plans__btn {
  max-width: 212px;
  margin: 0 auto;
}
.plans__title {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
.plans__subtitle {
  display: flex;
  max-width: 618px;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 60px;
}
.plans__columns {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-bottom: 60px;
}

.plans-item {
  background-color: #e6f4f1;
  border-radius: 20px;
  padding: 33px 30px 26px 30px;
  display: flex;
  flex-direction: column;
  max-width: 309px;
}
.plans-item__title {
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 28px;
  line-height: 120%;
  color: #53575a;
  margin-bottom: 9px;
}
.plans-item__subtitle {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 12px;
  line-height: 140%;
  color: #5a6770;
  margin-bottom: 38px;
}
.plans-item__list {
  margin-bottom: 24px;
}
.plans-item__list li {
  display: flex;
  gap: 6px;
  margin-bottom: 10px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
}
.plans-item__list li svg {
  width: 30px;
  height: 30px;
  margin-top: -3px;
}
.plans-item__price {
  margin-top: auto;
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 48px;
  line-height: 120%;
  color: #28445d;
  margin-bottom: 12px;
}
.plans-item__btn {
  padding: 12px 0;
}
.plans-item__btn:hover {
  background-color: rgba(40, 68, 93, 0.8);
  color: #e6f4f1;
}
.plans-item__btn--white {
  background-color: #e6f4f1;
  color: #28445d;
}
.plans-item__btn--white:hover {
  background-color: rgba(230, 244, 241, 0.8);
  color: #28445d;
}

.plans-item--black {
  background-color: #28445d;
}

.plans-item--color-white {
  color: #fff;
}

.plans-item--svg-white path {
  fill: #fff;
}

.faq {
  margin-bottom: 180px;
}
.faq__title {
  margin-bottom: 32px;
  display: flex;
  justify-content: center;
  text-align: center;
}
.faq__accordion {
  max-width: 968px;
  margin: 0 auto;
}

.accordion-item {
  border-bottom: 1px solid #d6dee1;
}
.accordion-item__header {
  display: flex;
  justify-content: space-between;
  padding: 25px 0 25px 0;
  cursor: pointer;
}
.accordion-item__header svg {
  transition: all 0.4s;
}
.accordion-item__title {
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 24px;
  line-height: 120%;
  color: #53575a;
}
.accordion-item__body {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: max-height 0.5s ease-out, opacity 0.5s ease-out;
  max-width: 858px;
}

.accordion-item.active {
  padding-bottom: 25px;
}

.accordion-item.active .accordion-item__body {
  max-height: 150px;
  opacity: 1;
}

.accordion-item.active .accordion-item__header {
  padding: 25px 0 15px 0;
}
.accordion-item.active .accordion-item__header svg {
  transform: rotate(45deg);
}

.accordion-item.active .accordion-item__title {
  margin-bottom: 13px;
}

.ready {
  margin-bottom: 60px;
}
.ready__wrapper {
  background-color: #28445d;
  border-radius: 20px;
  padding: 60px 235px;
}
.ready__title {
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 48px;
  line-height: 120%;
  text-align: center;
  color: #fff;
  margin-bottom: 34px;
}
.ready__btn {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 109%;
  letter-spacing: 0.04em;
  color: #28445d;
  background-color: #fff;
  border-radius: 6px;
  max-width: 243px;
  margin: 0 auto;
  transition: all 0.4s;
}
.ready__btn:hover {
  background-color: #e6f4f1;
}

.navigation {
  margin-bottom: 13px;
}
.navigation__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.navigation__list {
  display: flex;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
}
.navigation__list li {
  margin-right: 40px;
}
.navigation__list:not(:last-child) {
  margin-bottom: 58px;
}
.navigation__list:last-child {
  margin-left: 44px;
}
.navigation__list--top {
  margin-left: 49px;
}
.navigation__list--bottom {
  font-size: 12px;
}
.navigation__img {
  margin-bottom: 58px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.navigation__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.navigation__img--logo {
  width: 47px;
  margin-bottom: 10px;
}
.footer {
  padding: 13px 0;
  border-top: 1px solid #d6dee1;
  display: flex;
  justify-content: center;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 12px;
  line-height: 140%;
  text-align: center;
  color: #a5adb3;
}